.app-wrapper {
    display: grid;
    grid-template-rows: 100px 1fr;
    grid-template-columns: 150px 1fr;
    grid-column-gap: 40px;
    grid-row-gap: 40px;
    grid-template-areas: "h h"
                         "n c";
}

.app-wrapper-content {
    width: 1100px;
    grid-area: c;
    margin: 0 40px 0 20px;
    font-size: 0.8em;
}



